import { Store } from "../core/heropy";

const store = new Store({
  searchText: "",
  page: 1,
  pageMax: 1,
  movies: [],
  movie: {},
  loading: false,
  message: "Search for the movie title!",
});

export default store;
export const searchMovies = async (page) => {
  store.state.loading = true;
  store.state.page = page; //page ê°’ ë°”ë€ŒëŠ” ê²ƒ ì €ì¥í•˜ê¸°
  if (page === 1) {
    // store.state.page = 1;
    store.state.movies = [];
    store.state.message = "";
  }
  // ë¹„ë™ê¸°ë¡œ ë™ì‘í•˜ë‹ˆê¹Œ awaitë¥¼ ì‚¬ìš©í•´ì„œ fetch í•¨ìˆ˜ë¥¼ ì„œë²„ë¡œ ê°”ë‹¤ì˜¬ ë•Œ ê¸°ë‹¤ë ¤ì•¼í•¨
  // ë™ì‘ ìˆœì„œ : omdb í˜ì´ì§€ë¡œ ê²€ìƒ‰ ê²°ê³¼ë¥¼ ì „ì†¡ -> jsonìœ¼ë¡œ ë°›ì•„ì„œ ì½˜ì†”ì— ì¶œë ¥
  try {
    const res = await fetch(
      //by search ì´ê¸° ë•Œë¬¸ì— ì•„ë˜ &s ì‚¬ìš©
      `https://omdbapi.com?apikey=7035c60c&s=${store.state.searchText}&page=${page}`
    );
    const { Search, totalResults, Response, Error } = await res.json();
    if (Response === "True") {
      store.state.movies = [...store.state.movies, ...Search];
      store.state.pageMax = Math.ceil(Number(totalResults) / 10);
    } else {
      store.state.message = Error;
    }
  } catch (error) {
    console.log("searchMovies error: ", error);
  } finally {
    store.state.loading = false;
    // movieList.render();
  }
  // 1í˜ì´ì§€ì˜ ì˜í™”ì •ë³´ì™€ 2í˜ì´ì§€ì˜ ì •ë³´ë¥¼ Searchë¡œ ë°›ì•„ì˜¨ë‹¤. 3,4, ê³„ì† ë°›ì•„ì˜´
};
//ë§¤ê°œë³€ìˆ˜ id, fetchë¥¼ í•˜ê¸° ë•Œë¬¸ì— async ì¶”ê°€,
export const getMovieDetails = async (id) => {
  try {
    //by id or title(omdb api)ì´ê¸° ë•Œë¬¸ì— &i ì‚¬ìš©
    const res = await fetch(
      `https://omdbapi.com?apikey=7035c60c&i=${id}&plot=full`
    );
    store.state.movie = await res.json();
  } catch (error) {
    console.log("getMovieDetails error:", error);
  }
};
