// Component
export class Component {
  constructor(payload = {}) {
    const { tagName = "div", state = {}, props = {} } = payload;
    this.el = document.createElement(tagName);
    this.state = state;
    this.props = props;
    this.render();
  }
  render() {
    //...
  }
}

//Router
function routeRender(routes) {
  if (!location.hash) {
    history.replaceState(null, "", "/#/"); //íˆìŠ¤í† ë¦¬ì— ê¸°ë¡ ë‚¨ê¸°ì§€ì•Šê³ , /#/ í•´ì‹œ ì •ë³´ê°€ ì—†ì„ ë–„ ë„£ì–´ì¤Œ
  }

  const routerView = document.querySelector("router-view");
  // httpL//localhost:1234/#/about?name=heropy
  // #/about ê°€ í•„ìš”
  const [hash, queryString = ""] = location.hash.split("?");

  // a=123&b=456
  const query = queryString.split("&").reduce((acc, cur) => {
    const [key, value] = cur.split("=");
    acc[key] = value;
    return acc;
  }, {});
  history.replaceState(query, "", "");

  const currentRoute = routes.find((route) =>
    new RegExp(`${route.path}/?$`).test(hash)
  );
  routerView.innerHTML = "";
  routerView.append(new currentRoute.component().el);

  window.scrollTo(0, 0); //ìŠ¤í¬ë¡¤ ìµœìƒë‹¨ìœ¼ë¡œ ì´ë™
}

export function createRouter(routes) {
  return function () {
    window.addEventListener("popstate", () => {
      routeRender(routes);
    });
    routeRender(routes);
  };
}

///Store
export class Store {
  constructor(state) {
    this.state = {};
    this.observers = {}; //ë¹ˆ ê°ì²´ ìƒì„±
    for (const key in state) {
      // ì •ì˜í•˜ê³ ìí•˜ëŠ” ë°ì´í„°ë“¤ì˜ ìƒˆë¡œìš´ ê°’ë“¤ì´ í• ë‹¹ë ë•Œë§ˆë‹¤ í•„ìš”í•œ í•¨ìˆ˜ë¥¼ ì‹¤í–‰í•˜ê¸° ìœ„í•´
      Object.defineProperty(this.state, key, {
        get: () => {
          return state[key];
        },
        set: (val) => {
          state[key] = val;
          //observerëŠ” cbì˜ ê°’
          if (Array.isArray(this.observers[key])){
            this.observers[key].forEach(observer => observer(val))
          }
        },
      });
    }
  }
  //ìƒíƒœ êµ¬ë…
  subscribe(key, cb) {
    // ì–´ë–¤ ë°ì´í„°ë¥¼ ê°ì‹œí•  ê²ƒì´ê³ (key), ë³€í™”í•˜ë©´ cbë¼ëŠ” í•¨ìˆ˜ ì‹¤í–‰
    // this.observers['message'] = () => {}
    // { message : () => {} }
    // version2 { message : [() => {}, ()=> {}, () => {}]} ë©”ì„¸ì§€ì˜ ê°ì²´ë¥¼ ì—¬ëŸ¬ê°œ ë§Œë“¤ì–´ì£¼ê¸° ìœ„í•´ ë°°ì—´ ë°ì´í„° ìƒì„±
    // pushê°’ì€ ë°°ì—´ ë°ì´í„°ì—ì„œë§Œ ì‚¬ìš© ê°€ëŠ¥
    // Array.isArray()ë¥¼ í†µí•´ì„œ ì¸ìê°’ì´ ë°°ì—´ë°ì´í„°ë¼ë©´ ? ë’¤ë¥¼ ì‹¤í–‰í•˜ê³  ì•„ë‹ˆë¼ë©´ : ë’¤ë¥¼ ì‹¤í–‰í•œë‹¤.
    Array.isArray(this.observers[key]) 
    ? this.observers[key].push(cb)
    : this.observers[key] = [cb];
  }
}

